.nav {
  &__link {
    position: relative;
    color: inherit;
    text-decoration: none;
    @include styleFont(22px, 700, 22px);

    &::after {
      content: " ";
      position: absolute;
      bottom: -5px;
      display: block;
      height: 1px;
      width: 100%;
      background-color: $main-text-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin: left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
}
